import { mapMutations,mapGetters } from "vuex";
import login from "../userlogin/login";
  export default {
      data() {
          return {
              flag: false,
              list: [],   // 
              price: null,
              seleced:true,  // 
          }
      },
      filters:{
        countPrice(count,n){
          return '' + parseFloat(count).toFixed(n)
        }
      },
      mounted() {
          this.localhostList();
          this.changeAllChecked();
      },
      computed:{
              ...mapGetters(["totlePrice","getGoodsChecked","getGoodsAmount",]),
      },
      methods: {
          ...mapMutations(["updateGoodsCount","changeSelectState"]),
          goClick(){
            this.$router.go(-1);
          },
          showPopup(){
              if (this.list.length !== 0)
                  return this.$message({showClose:true,message: '', type: 'success', duration: 3000});
              if (this.list.length === 0)
                  return this.$message({ showClose:true,message: '', type: 'error', duration: 3000 })
          },
          localhostList:function(){
              //
              return this.list = this.$store.state.cart;
          },
          add(){
              // store 
              this.$store.commit("updateGoodsCount",this.list);
          },
          //
          cuts:function(number){
              if(this.list[number].count<=1) return;
              this.list[number].count--;
              console.log(this.list[length].count);
              //add 
              this.add();
          },
          //
          plus:function(number){
              this.list[number].count++;
              console.log(this.list[length].count);
              //add 
              this.add();
          },
          // 
          changeAllChecked:function() {
              for (let i = 0; i <= this.list[length]; i++){
                  if (this.list[i].seleced === true){
                      console.log(this.list[i].seleced);
                      this.seleced = true
                  }else{
                      this.seleced = false
                  }
              }
              //
              // if (this.allChecked === false){
              //     for (let i = 0; i < this.list.length; i++) {
              //         let item = this.list[i];
              //         item.seleced = true
              //     }
              // }else{
              //     //
              //     for (let i = 0; i<this.list.length;i++){
              //         let item = this.list[i];
              //         item.seleced = false;
              //     }
              // }
              // this.allChecked = !this.allChecked;
          },
          //storecart
          deleteCart(id) {
              this.$store.commit('deleteStoreCart',id);
          },
      },
  }



