export default {
  props:{
    chartData:{
      type: Array,
      default: ()=>{
        return
      }
    }
  },
  data(){
    return{
      isLoad: false,
    }
  },
  methods:{
    imageLoad(){
      /*
      *
      * */
      if(!this.isLoad){
        this.$emit('itemImageLoad');
        this.isLoad = true;
      }
    }
  }
}



