import Header from "@/components/commion/content/regionTopBottom/Header";
import { getSnake, getSnakeGoods } from "@/api/request"; //=> axios
import Scroll from "@/components/commion/scroll/Scroll"; //=> BetterScroll
import TopRocket from "@/components/commion/BackTool/TopRocket"; //=> 
import Recommend from "../commion/Label/Recommend"; //=> 
import Chart from "../commion/Rotation/Chart"; //=> 
import TabControl from "@/components/commion/content/tabControl/TabControl";
import Commodity from "../commion/List/Commodity"; //=> 
export default {
  components: {
    Scroll, //=> BetterScroll
    TopRocket, //=> 
    Recommend, //=> 
    Chart, //=> 
    Commodity, //=> 
    TabControl,
    Header,
  },
  data() {
    return {
      isanimated: true,
      tabClickData: ["", "", ""],
      title1: "",
      lunbotuList: [],
      isShowBackTop: false, //=> 
      isTabFixed: false, //=> 
      taboffsetTop: 0,
      goods: {
        clothing: { page: 0, list: ["1"] },
        souper: { page: 0, list: ["2"] },
        souper2: { page: 0, list: ["3"] }
      },
      currentType: "clothing",
      scrollY: 0,
    };
  },

  computed: {
    showGoods() {
      return this.goods[this.currentType].list;
    }
  },

  beforeCreate() {
    console.log("HomeContainer beforeCreate");
  },

  created() {
    //=> 
    getSnake()
      .then(result => {
        this.lunbotuList = result.data.message;
      })
      .catch(err => {
        return Promise.reject(err);
      });

    //=> 
    this.getSnakeGoods("clothing");
    this.getSnakeGoods("souper");
    this.getSnakeGoods("souper2");
  },

  methods: {
    tabClick(index) {
      switch (index) {
        case 0:
          this.currentType = "clothing";
          break;
        case 1:
          this.currentType = "souper";
          break;
        case 2:
          this.currentType = "souper2";
          break;
      }
      this.$refs.tabControl01.currentIndex = this.$refs.tabControl02.currentIndex = index;
    },

    getSnakeGoods(type) {
      getSnakeGoods()
        .then(result => {
          this.goods[type].list = result.data[type];
        })
        .catch(err => {
          return Promise.reject(err);
        });
    },

    commodity() {
      this.utils.debounce(this.$refs.scroll.refresh.refresh);
    },

    imageLoad() {
      /*
       *  tabControl  tabOffsetTop
       *  $el 
       * */
      this.taboffsetTop = this.$refs.tabControl02.$el.offsetTop;
    },

    //=> 
    loadMore() {
      console.log("------");
      this.$refs.scroll.finishPullUp();
    },

    //=> 
    contentScroll(position) {
      /*
       * 
       * position.yscrolly
       * y500this.backtoptrue
       * */
      this.isShowBackTop = (-position.y) > 500;

      /*
       * tabControl
       */
      this.isTabFixed = (-position.y) > this.taboffsetTop;


    },

    /*
     * x,y,
     * x:0,y:60,itme:600
     * y60
     *  */
    backtopClick() {
      this.$refs.scroll.scrollTo(0, 60, 600);
    }
  },

  //=> 
  destroyed() {
    console.log("HomeContatiner destroyed");
  },

  /*
  *  
  * */ 
  activated(){
    console.log('activatd');
      this.$refs.scroll.scrollTo(0,this.scrollY,50);
      this.$refs.scroll.refresh();
  },

  /*
  * 
  * */ 
  deactivated(){
    console.log('deactivatd');
    this.scrollY =  this.$refs.scroll.getScrollY();
    console.log("" + this.scrollY);
  }

};



