import { Store } from 'vuex'
export default {
    props:{
        isDataList:{
          type: Array,
          default: ()=>{
            return null
          }
        },
        textData:{
          type: String,
          default: function (){
            return ""
          }
        }
    },
    data(){
      return{
        childrenData: "",
        loadimg: false,
      }
    },
    created() {
      console.log(this.textData)
    },
    mounted(){
      this.$emit('childrenData',this.childrenData)
    },
    methods:{
      imageLoad(){
        this.$emit('itemImageLoad',this.loadimg)
      }
    }
}



