import { Toast } from 'mint-ui'
import qs from 'qs'
import { mapMutations } from "vuex";
  export default {
      data(){
        return{
            loginForm:{
              username:'',
              password:'',
            },
        }
      },
      created(){
      },
      methods: {
          ...mapMutations(["changeUserInfo","$_setToken"]),
          login() {
              let params = new URLSearchParams();
              params.append('username',this.loginForm.username);
              params.append('password',this.loginForm.password);
              console.log(":" + params);
              let val = {
                username:this.loginForm.username,
                password:this.loginForm.password
              };
                   axios({
                          method: 'post',
                          url: '/api/goods',
                          data: qs.stringify.val,
                          headers: {'Content-Type': 'application/json'},   //header:Content-Type:application/json,json
                      }).then((res) => {    //  res
                          console.log(res);
                          if (res.data.errno.username === this.loginForm.username){
                            if(res.data.errno.password === this.loginForm.password){
                              this.initUserInfo();
                              return this.$message.success({message: this.loginForm.username + '', duration: 1500});   // ui
                            }else{
                              return this.$message.error({message: '', duration: 3000});
                            }
                          } else {
                              console.log(res.data);
                             return this.$message.error({message: '', duration: 3000});
                            }
                      }).catch((error) => {    //  error
                          this.$message.error({message: '', duration: 3000});
                          console.log(error)
                      });

          },
          initUserInfo(){
            axios.get('/static/Snake.json').then(res => {
                if (res.data.status === 0){
                    const userInfo = this.loginForm;   //data
                    console.log(res.data);
                    this.$store.commit('$_setToken',JSON.stringify(userInfo));// $_setToken
                    localStorage.setItem("userInfo", JSON.stringify(userInfo)); //localStorage
                    this.$store.commit("changeUserInfo", JSON.stringify(userInfo));  //commitmutations  
                    this.$router.push({name: 'HomeContainer'});  // 

                }
            })
          },
      }
  }



