import {Toast} from "mint-ui";
import { mapMutations } from "vuex"
  export default {
    inject:['reload'],      //=>
      data(){
          return{
              //ID url
              id: this.$route.params.id,
              name:'',
              neirong:'',   //
              pinglunlist:[],
              comment:[
                  // {name:'',date:Date.now(),neirong:''},
                  // {name:'',date:Date.now(),neirong:''},
                  // {name:'',date:Date.now(),neirong:''}
              ], //
          }
      },
      created(){
          this.loadComments();
      },
      methods:{
          ...mapMutations(["Storingpinglun"]),
          getComments(){
              let pinglun  = '';
              axios.get('./static/Snake.json').then(result => {
              if (result.data.status === 0){
                      //data
                      pinglun = result.data.clothing;
                      pinglun.filter((item) => {
                          return this.id === item.id;
                      });
                  this.comment = pinglun[length].pinglun;
              }else{
                  Toast('')
              }
          })
          },
          postComment(){
                let comment = {
                    data: Date.now(),
                    name: this.name,
                    neirong : this.neirong };
              //localStorage
                let list = JSON.parse(localStorage.getItem('pinglun') || '[]');
                list.push(comment);
                // list.unshift(comment);
                //
                localStorage.setItem('pinglun',JSON.stringify(list));
                this.neirong = '';
                this.$emit('update');
                Toast({ message:'', duration:1500});
                this.reload();
          },
          loadComments(){    // localStorage 
              let pinglunlist = JSON.parse(localStorage.getItem('pinglun' || '[]'));
              this.pinglunlist = pinglunlist;
          }
      },
  }



