import Header from "@/components/commion/content/regionTopBottom/Header";
import { getSnake, getSnakeGoods } from "@/api/request"; //=> axios
  export default {
    components:{
      Header,
    },
      data() {
          return {
              isLoading:false,
              keywords:'',
              list:[],
          };
      },
      created() {
          getSnake().then((result) => {
            this.list = result.data.clothing;
          }).catch((err) => {
           return Promise.reject(err);
          });
          this.search();
      },
      methods: {
        searchTitle(titledata){
          let searchList = [];
          searchList = this.list.filter((item) => {
            if(item.category.includes(titledata)){
              return item;
            }
          });
          return searchList;
        },
          onRefresh() {
              setTimeout(() => {
                  this.isLoading = false;
                  Toast("")
              }, 2000);
          },
          //
          search(keywords){
              let newlist = [];
              //For each some filter findIndex
              //
              //
              // this.list.forEach(item => {
              // 	if(item.name.indexOf(keywords) != -1){
              // 	newlist.push(item)
              // 	}
              // })
              // return newlist
              //
              //ES6String.prototype.includes('')
              //truefalse
              // contain
              newlist = this.list.filter(item => {
                  if (item.category.includes(keywords)){
                      return item
                  }
              });
              return newlist
          }
      },
  }



