import { Toast } from 'mint-ui'
import { getSnake, getSnakeGoods } from "@/api/request"; //=> axios
import Header from "@/components/commion/content/regionTopBottom/Header";
import NewsList from "../NewsList";
// 1.
import comment from "./subcomponents/comment";
//2.
import comment_text from "./subcomponents/comment-text";
    export default {
      components:{  // 
            Header,
            'comment-box':comment,
            'comment-text':comment_text,
        },
        data(){
            return{
                //ID url
                id: this.$route.params.id,
                newsinfo:{ },
                info:{},
            };
        },
        created(){
            this.getNewsInfo();
        },
        methods:{
            text(){
                this.$router.go(-1);           //=>
            },
            getNewsInfo(){  //
                getSnake().then(result => {
                    if (result.data.status === 0){
                        //data
                        this.newsinfo = result.data.clothing.filter((item) => {
                          return this.id === item.id
                        })
                        // id
                        // this.info = this.newsinfo.filter((item) => {
                        //     return this.id === item.id
                        // })
                    }else{
                        Toast('')
                    }
                })
            },
        },
    }



