export default {
    data(){
        return{
            //ID url
            id: this.$route.params.id,
            newsinfo:[],
            info:[],
        }
    },
    created(){
        this.getNewsInfo();
    },
    methods:{
        getNewsInfo(){  //
            let JSONLIST = './static/Snake.json';
            axios.get(JSONLIST).then(result => {
                if (result.data.status === 0){
                    //data
                    this.newsinfo = result.data.clothing;
                    // id
                    this.info = this.newsinfo.filter((item) => {
                        return this.id === item.id
                    })
                }else{
                    Toast('')
                }
            })
        },
    },
}



