import Header from "@/components/commion/content/regionTopBottom/Header";
import { getSnake } from "@/api/request"; //=> axios
import Commodity from "../commion/List/Commodity";      //
import Scroll from "../commion/scroll/Scroll";          //Scroll
import TopRocket from "../commion/BackTool/TopRocket";  //
import { Toast } from "mint-ui";
  export default {
    components:{
      Header,
      Commodity,    //
      Scroll,       //
      TopRocket,    //
    },
      data(){
          return{
              id: this.$route.params.id,
              Clothes:[],
              list:[],
              isShowBackTop: false,
          }
      },
      created(){
          this.getClothes();
      },
      methods:{
        imageLoad(){
          this.utils.debounce(this.$refs.scroll.refresh.refresh);
        },
          onRefresh() {
              setTimeout(() => {
                  this.isLoading = false;
                  Toast("")
              }, 2000);
          },
          getClothes(){
              this.$forceUpdate();
              getSnake().then((result) => {
                  if (result.data.status === 0){
                      this.list = result.data.souper;
                      this.Clothes = result.data.clothing;
                      // console.log(this.list);
                      this.$forceUpdate();
                  }else{
                      Toast({message: '',duration: 1500});
                  }
              }).catch((error) =>{
                  Toast({message: 'result.data.status' + "", type: 'success', duration: 1500});
                  // console.log(error)
              })
          },
        loadMore(){
          console.log("---------")
        },
        contentScroll(position){
            this.isShowBackTop = (-position.y) > 500
        },
        backTopClick(){
            this.$refs.scroll.scrollTo(0,70,600)
        }
      }
  }



