export default {
  props:{
    dataList:{
      type: Array,
      default: () => {
        return null
      }
    },
  },
  filters:{
    dataPrice(property, count) {
      return "" + parseFloat(property).toFixed(count);
    }
  },
  methods:{
    imageLoad(){
      this.$emit('itemImageLoad');
    }
  },
}



