import { mapMutations } from "vuex";
export default {
    data(){
      return{
          rum:4.8,
          activeName: '1',
          value: new Date(),
          fole:true
      }
    },
    created(){
    },
    methods: {
        ...mapMutations(["$_removeStorage"]),
        alertQuit(){
            localStorage.clear();
            this.$store.commit("$_removeStorage");
            this.$message.success({message: '', duration: 1500});   // ui
            this.$router.push({ name:"login" })
        }

    }
}



