import { Toast } from "mint-ui";
import { mapMutations } from "vuex"
  export default {
      data(){
          return{
              flag:false,
              id:this.$route.params.id,
              img:{},   //
              detailsimg:[],
              info:{},  //
              list:{}, //
              selectedCount:1,  //
          }
      },
      mounted(){
          this.log();
          this.DetailsImg();
      },
      methods:{
          text(){
              this.$router.go(-1);           //=>
          },
        log(){
            console.log("id:" + this.id)
        },
          showPopup(){
              if (this.list.length !== 0)
                  return this.$message({showClose:true,message: '', type: 'success', duration: 3000});
          },
          DetailsImg(){
              this.$forceUpdate();
            //json
              let localJSON = './static/Snake.json';
              axios.get(localJSON).then( response => {
                  this.detailsimg = response.data.clothing;
                  //
                  this.info = this.detailsimg.filter((item) => {
                      return item.id === this.id;
                  });
                  this.img = this.info[length].pinglun;
                  // console.log(this.img)
              });
          },
          // 1.vuex
          ...mapMutations(["addToCar"]),
          DetailsCalick(){
            this.list = this.info;
              this.addToCar({
                  id:this.id,
                  img:this.info[length].img_url,
                  price_title:this.list[length].price_item,
                  price:this.list[length].price,
                  count:this.selectedCount,
                  seleced:false,
              });
          },

          //
          // el
          // DOM JS DOM 
          // el  document.getElementById('') JS DOM
          beforeEnter(el) {
              // 
              // 
              // 
              el.style.transform = 'translate(0,0)';
              console.log('---')
          },
          enter(el,done) {
              //,
              el.offsetWidth;//offsetHeightoffsetLeft
              //
              el.style.transform = 'translate(150px,200px)';
              el.style.transition = 'all 1s ease';
              console.log('---');
              //  done afterEnter 
              // done  afterEnter 
              done();
          },
          afterEnter(el) {
              //
              //  flag 
              // Vue 
              // flag false -> true ;   true -> false
              // 
              //  flag  false
              // flag = false -> true
              this.flag = !this.flag;
              console.log('---')
          }
      }
  }



