
import Header from "@/components/commion/content/regionTopBottom/Header";
import Scroll from "@/components/commion/scroll/Scroll"; 
import { getSnake } from "@/api/request"; 
import Loading from 'vux/src/components/loading/index.vue'

import { mapState } from 'vuex'
import { Toast } from 'mint-ui'
export default {
components:{
Header,
Scroll,

},
data(){
return{

selected: '1', //1
list:[],
souper:[],
}
},
created() {
this.souperjson();
},
computed:{
...mapState({
isLoadings: state => state.vux.isLoading
})
},
methods:{
text(){
this.$router.go(-1);           
},






souperjson(){
getSnake().then((item) => {
if (item.data.status === 0){
this.list = item.data.souper;
this.souper = item.data.souper2;
console.log(this.list);
console.log(this.souper);
}else{
Toast('')
}
})
}
},
}



